  padding: 0.4rem 0.625rem;
  /* The 6px vertically centers text on FF, ignored by Webkit */
  border-radius: 3px;
  box-shadow: none;
  background: #fff;
}

textarea {
  min-height: 65px;
  padding-top: 0.4rem;
  padding-bottom: 0.4rem;
  resize: vertical;
}

input:focus,
textarea:focus,
select:focus {
  border: 1px solid #337ab7;
  outline: 0;
}

